.header {
  position: relative;
  z-index: 2;
}

.l-header {
  width: 100%;
  height: 90px;
  background-color: #288f4d;
  display: flex;
  justify-content: start;
  align-items: center;

  &__title {
    @include sp {
      margin: 0 0 0 20px;
    }
  }

  &__wrap {
    width: 960px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @include sp {
      width: inherit;
    }
  }
}

.header__logo {
  width: 365px;
  height: 63px;

  @include sp {
    width: 210px;
    height: 36px;
  }
}
